let Element = require('../common/element');


module.exports = function (a, n, q) {//an
   let ans = new Element(0n,0n,1,q);
   while(n != 0n){
  		  if((n&1n) == 1n) {       //n1
  	          ans.mul(a);  //ansa
  	      }
  	    a.mul(a);        //a
  	    n = n >> 1n;       //n
  	}
  	return ans;
}
	