let GTelement = require('../common/GTElement');


module.exports = function (a, n, q) {//an
   let ans = new GTelement(1n,0n,q);
   while(n != 0n){
  		  if((n&1n) == 1n) {       //n1
  	          ans.mul(a);  //ansa
  	      }
  	    a.mul(a);        //a
  	    n = n >> 1n;       //n
  	}
  	return ans;
}
	